// Input.tsx
import clsx from 'clsx';
import { focusRing } from '../../../tailwind/styles/focus';
import { InputProps, InputScope } from './input.interface';
import { borderRadius } from '../../../tailwind/styles/border-radius';
import { FunctionalIcon } from '../icon/Icon';

export const Input: React.FC<InputProps> = ({
  id,
  type,
  value,
  error,
  label,
  disabled = false,
  icon,
  readOnly = false,
  hasInfoIcon = false,
  message,
  onIconToggle,
  onIconInfoClick,
  onChange,
  ...props
}) => {
  const searchCancel = clsx(
    'search-cancel:appearance-none',
    'search-cancel:w-5',
    'search-cancel:h-5',
    'search-cancel:border-[15px]',
    'search-cancel:border-solid',
    'search-cancel:border-transparent',
    'search-cancel:cursor-pointer',
    'search-cancel:bg-no-repeat',
    'search-cancel:mr-[-16px]',
    'search-cancel:bg-[url(/src/assets/search-cancel.svg)]',
  );

  const calendarPicker = clsx(
    'calendar-picker:appearance-none',
    'calendar-picker:w-5',
    'calendar-picker:h-5',
    'calendar-picker:p-0',
    'calendar-picker:border-x-[16px]',
    'calendar-picker:border-solid',
    'calendar-picker:border-transparent',
    'calendar-picker:cursor-pointer',
    'calendar-picker:bg-no-repeat',
    'calendar-picker:mr-[-16px]',
    'calendar-picker:bg-[url(/src/assets/calendar.svg)]',
  );

  return (
    <span
      className={clsx(
        InputScope,
        'inline-flex',
        'flex-col',
        'flex-wrap',
        'items-stretch',
        'justify-start',
        'relative',
        [disabled ? 'cursor-not-allowed opacity-zep-40' : 'cursor-pointer'],
      )}
      data-testid="zep-input"
    >
      {label && (
        <label htmlFor={id} className={clsx('mb-zep-0.5')}>
          {label}
        </label>
      )}
      <div className={clsx('inline-flex', 'flex-row', 'flex-nowrap', 'items-center', 'gap-zep-0.5')}>
        <div className={clsx('relative', 'w-full')}>
          <input
            id={id}
            value={value}
            type={type}
            onChange={!disabled ? onChange : undefined}
            aria-describedby={`aria-describedby-${id}`}
            className={clsx(
              borderRadius,
              focusRing,
              'border',
              'w-full',
              'h-zep-3',
              'px-zep-1',
              'leading-[48px]',
              'leading-[22px]',
              'focus-visible:ring-offset-1',
              'placeholder-zep-typography-dark-100 placeholder-opacity-70',
              'invalid:[&:not(:placeholder-shown):not(:focus)]:border-zep-error',
              'invalid:[&:not(:placeholder-shown):not(:focus)]:border-zep-2',
              { 'border-zep-darkgrey-500': !error },
              { 'border-zep-2 border-zep-error': error },
              { 'cursor-not-allowed text-zep-typography-dark-100 bg-zep-greyScale-500': readOnly },
              { [calendarPicker]: type === 'date' },
              { [searchCancel]: type === 'search' },
            )}
            disabled={disabled}
            data-testid="zep-input-field"
            {...props}
          />
          {icon && (
            <FunctionalIcon
              onClick={onIconToggle}
              className={clsx('absolute', 'top-0', 'bottom-0', 'right-zep-1', 'h-full', {
                'cursor-pointer': onIconToggle,
              })}
              name={icon}
            />
          )}
        </div>
        {hasInfoIcon && <FunctionalIcon onClick={onIconInfoClick} name="Info" />}
      </div>
      {message && (
        <div
          className={clsx('inline-flex', 'flex-row', 'items-center', 'gap-zep-0.25', 'text-zep-error', 'mt-zep-0.25')}
        >
          <FunctionalIcon name="Warning" />
          <span id={`aria-describedby-${id}`}>{message}</span>
        </div>
      )}
    </span>
  );
};
